html {
	background: #e2e5ff;
}

.title {
text-align: center;
margin-bottom: 30px;
font-size: 4em;
}

.buttons {
	align-items: center;
	display: flex;
	justify-content: center;
	margin-top: 20px;
}

.button {
	color: #fff !important;
	border: none;
	background: #62a87c;
	padding: 15px;
	margin: 15px;
	border-radius: 5px;
	font-size: 2em;
	
}

.button:hover {
	color: #fff;
	border: none;
}

.button:focus {
	outline: none;
}
